@import "../global/global.scss";

.intro {
    background-color: $background;
    display: flex;

    @include mobile {
        flex-direction: column;
        align-items: center;
    }

    .left {
        flex: 0.5;
        overflow: hidden;

        .imgContainer {
            width: 700px;
            height: 700px;
            background-color: $primary;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile {
                align-items: flex-start;
            }

            img {
                height: 90%;

                @include mobile {
                    height: 50%;
                }
            }
        }
    }
    .right {
        flex: 0.5;
        position: relative;

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            color: $white;
            @include mobile {
                padding-left: 0;
                align-items: center;
            }

            h1 {
                font-size: 6rem;
                margin: 10px 0;

                @include mobile {
                    font-size: 40px;
                }
            }
            h2 {
                font-size: 3rem;
            }
            h3 {
                font-size: 2.5rem;

                @include mobile {
                    font-size: 20px;
                }

                span {
                    font-size: inherit;
                    color: $primary;
                }

                .ityped-cursor {
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50% {
                        opacity: 1;
                    }
                    100% {
                        opacity: 0;
                    }
                }
            }
        }
    }
}
