@import "../global/global.scss";

.projects {
    background-color: $background;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $white;

    h2 {
        font-size: 4rem;

        @include mobile {
            font-size: 20px;
        }
    }

    ul {
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile {
            margin: 10px 0;
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    .container {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile {
            width: 100%;
        }

        .item {
            width: 300px;
            height: 200px;
            border-radius: 20px;
            border: 2px solid $background-overlay;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: $white;
            position: relative;
            transition: all 0.3s ease;
            cursor: pointer;

            @include mobile {
                width: 130px;
                height: 100px;
            }

            h3 {
                position: absolute;
                font-size: 20px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover {
                background-color: $primary-variant;
                img {
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}
